import React from 'react';
import { DialogTitle, Dialog } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

export interface Props {
  onClose: Function;
  open: boolean;
}

const useStyles = makeStyles((theme) => ({}));

export const NotSignedInDialog: React.FC<Props> = ({ onClose, open }) => {
  const classes = useStyles();

  const handleClose = () => {
    onClose();
  };

  return (
    <Dialog onClose={handleClose} aria-labelledby="dialog-title" open={open}>
      <DialogTitle id="dialog-title">You're not signed in yet</DialogTitle>
    </Dialog>
  );
};

export default NotSignedInDialog;
